<template>
	<div class="page">
		<p-page-header title=""></p-page-header>
		<el-form inline :model="queryForm">
			<el-form-item size="mini" label="">
				<el-input placeholder="" clearable v-model="queryForm.name"></el-input>
			</el-form-item>
			<el-form-item size="mini" label="">
				<el-select placeholder="" clearable v-model.trim="queryForm.couponTypeId">
					<el-option v-for="item in couponTypeList" 
						:key="''+item.id"
						:label="item.name"
						:value="item.id"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item size="mini">
				<el-button type="primary" :loading="queryLoading" @click="handleClick" icon="el-icon-search"></el-button>
			</el-form-item>
			<el-form-item size="mini">
				<el-button type="success"  @click="handleAdd" icon="el-icon-plus"></el-button>
			</el-form-item>
			<el-form-item size="mini">
				<el-button type="success"  @click="handleAddMultiple" plain icon="el-icon-plus"></el-button>
			</el-form-item>
		</el-form>
		<el-table
			border
			size="mini"
			:data="list">
			<el-table-column label="id" prop="id"></el-table-column>
			<el-table-column label="" prop="name"></el-table-column>
			<el-table-column label="" >
				<template v-slot="{row}">
					<el-tag  size="small">
						{{row.couponTypeName}}
					</el-tag>
				</template>
			</el-table-column>
			<el-table-column label="" prop="name">
				<template v-slot="{row}">
					<el-tag size="mini">
						{{formatTime(row.targetTime)}}
					</el-tag>
				</template>
			</el-table-column>
			<el-table-column label="" show-overflow-tooltip prop="remark">
			</el-table-column>
			<el-table-column width="300px" label="" >
				<template v-slot="{row}">
					<el-button 
						size="mini" 
						icon="el-icon-edit" 
						plain 
						@click="handlePeviewQrcode(row)" 
						type="warning"></el-button>
					<el-button size="mini" icon="el-icon-edit" @click="handleEdit(row.id)" type="warning"></el-button>
					<el-button size="mini" @click="handleRemove(row.id)" icon="el-icon-remove" type="danger"></el-button>
				</template>
			</el-table-column>
		</el-table>
		
		<el-pagination
			@size-change="handleSizeChange"
			@current-change="handleCurrentChange"
			:current-page="page.pno"
			:page-size="page.psize"
			layout="total, sizes, prev, pager, next, jumper"
			:total="page.totalElements">
		</el-pagination>
		<el-dialog
				title=""
				width="300px"
				style="text-align: center;"
				:visible.sync="dialogVisible">
			<canvas 
				ref="canvas" 
				id="canvas"  
				width="200px"></canvas>
		</el-dialog>
	</div>
</template>

<script>
	import qrcode from 'qrcode'
	console.log(qrcode)
	// vuexstateactionsstorestateactions
	import { mapState,mapActions } from 'vuex'
	export default{
		// keep-alive
		name:'goods',
		data(){
			return {
				// 
				queryForm:{
					name:'',
					couponTypeId:'',
					isOnSale:'',
					pno:1,
					psize:10
				},
				// 
				queryLoading:false,
				//
				dialogVisible:false,
				//
				dialogForm:{
					info:"",
				}
			}
		},
		computed:{
			...mapState('couponModel',['list','page','couponTypeList']),
			// 
			formatTime(){
				return function(time){
					let d = new Date(Number(time));
					return `${d.getFullYear()}-${d.getMonth()+1}-${d.getDate()} ${d.getHours()}:${d.getMinutes()}:${d.getSeconds()}`
				}
			}
		},
		// created
		async created(){
			await this.getCouponTypeListAll()
			await this.getListForPage(this.queryForm)
		},
		// keep-aliveactivated
		async activated(){
			await this.getCouponTypeListAll()
			await this.getListForPage(this.queryForm)
		},
		methods:{
			...mapActions('couponModel',['getListForPage','deleteById','getCouponTypeListAll','setIsOnSale']),
			// 
			async handleClick(){
				this.queryLoading = true;
				this.queryForm.pno = 1;
				await this.getListForPage(this.queryForm)
				this.queryLoading = false;
			},
			// 
			async handleSizeChange(psize){
				this.queryForm.psize = psize
				this.queryForm.pno = 1;
				await this.getListForPage(this.queryForm)
			},
			// 
			async handleCurrentChange(pno){
				this.queryForm.pno = pno
				await this.getListForPage(this.queryForm)
			},
			// 
			handleAdd(){
				this.$router.push('/coupon-add')
			},
			async handleRemove(id){
				// $confirm
				let confirm =await  this.$confirm('','',{
					type:'warning'
				}).catch(err => err)
				if(confirm == 'confirm'){
					// 
					//
					await this.deleteById(id)
					//
					await this.getListForPage(this.queryForm)
				}
			},
			// 
			handleEdit(id){
				this.$router.push({path:'/coupon-edit',query:{id}})
			},
			async handleSetIsOnSale(row){
				let message
				let isOnSale
				
				if(row.isOnSale == 1){
					message = ''
					isOnSale = 0
				}else{
					message = ''
					isOnSale = 1
				}
				let confirm = await this.$confirm(message,"",{type:'warning'}).catch(err=>err)
				if(confirm == 'confirm'){
					await this.setIsOnSale({
						id:row.id,
						isOnSale:isOnSale
					})
					await this.getListForPage(this.queryForm)
				}
			},
			handleAddMultiple(){
				this.$router.push('/coupon-add-multiple')
			},
			async handlePeviewQrcode(row){
				//json
				this.dialogForm.info = JSON.stringify(row)
				//
				this.dialogVisible = true
				//dialogtrue
				//
				//canvas
				//$nextTick
				//
				//canvas
				await this.$nextTick()
				//domcanvas
				// let c1 = document.querySelector('#canvas')
				// console.log(c1)
				//refcanvas
				let c2 = this.$refs.canvas;
				console.log(c2)
				qrcode.toCanvas(c2,this.dialogForm.info ,(res)=>{
					if(!res){
						console.log('success')
					}
				})
			}
		}
	}
</script>

